export default {
    data: () => ({
        // Variables para grabación de video
        video: null,
        videoaudio: null,
        grabando: false,
        disabled: false,
        promisegrabando: null,
        resolvegrabando: null,
        audio: true,
        textoaudio: '',
        msgerror: [],
        dialogError: false,
        videograbado: false
    }),
    methods: {
        cargarCamara() {
            window.scrollTo({
                top: 0,
                left: 0,
                behavior: 'smooth'
            });
            navigator.mediaDevices.getUserMedia({
                video: { deviceId: 'default' },
                audio: false
            }).then(function(stream) {
                let video = document.getElementById('preview2');
                video.srcObject = stream;
                video.muted = true;
                video.controls = false;
                video.onloadedmetadata = function(e) {
                    video.play();
                };
            }).catch(function(err) {
                // deal with an error (such as no webcam)
                console.log(err);
                this.dialogFotoError = true;
                this.documentosSubidos = true;
            });
        },
        wait(delayInMS) {
            var self = this;
            return new Promise(resolve => {
                self.resolvegrabando = resolve;
                console.log(self.resolvegrabando);
                var variable = setTimeout(resolve, delayInMS);
                self.promisegrabando = variable;
            });
        },
        startRecording(stream, lengthInMS) {
            this.grabando = true;
            let recorder = new MediaRecorder(stream);
            let data = [];
          
            recorder.ondataavailable = event => data.push(event.data);
            recorder.start();
            console.log(recorder.state + " for " + (lengthInMS/1000) + " seconds...");
          
            let stopped = new Promise((resolve, reject) => {
              recorder.onstop = resolve;
              recorder.onerror = event => reject(event.name);
            });
          
            var self = this;
            let recorded = this.wait(lengthInMS).then( () => {
              console.log('deteniendo grabacion desde acciones after promise');
              recorder.stop();
              self.videograbado = true;
            });
          
            return Promise.all([
              stopped,
              recorded
            ])
            .then(() => data);
        },
        stop(stream) {
            this.grabando = false;
            this.disabled = true;
            stream.getTracks().forEach(track => track.stop());
            clearTimeout(this.promisegrabando);
            console.log('timeout cleared');
            this.resolvegrabando();
            console.log('resolve de stop funcion');
        },
        stopBtn(){
            this.stop(
                document.getElementById('preview').srcObject
            );
        },
        cambiarVideos() {
            var preview = document.getElementById('preview');
            var video = document.getElementById('preview2');
            var swap = preview.style.display == 'none';
            preview.style.display = (swap) ? 'initial' : 'none';
            video.style.display = (swap) ? 'none' : 'initial';
        },
        recordBtn() {
            var self = this;
            this.cambiarVideos();
            navigator.mediaDevices.getUserMedia({
                video: { deviceId: 'default' },
                audio: true
            }).then(function(stream) {
                let video = document.getElementById('preview');
                self.video = null;
                video.srcObject = stream;
                video.muted = true;
                video.controls = false;
                console.log(video);
                video.captureStream = video.captureStream || video.mozCaptureStream
                return new Promise(resolve => video.onplaying = resolve);
            }).then(() => {
                let video = document.getElementById('preview');
                self.startRecording(
                    video.captureStream(), 10000
                ).then(recordedChunks => {
                    self.grabando = false;
                    self.disabled = false;
                    let recordedBlob = new Blob(recordedChunks, { type: "video/mp4" });
                    self.video = recordedBlob;
                    //console.log(self.video);
                    //console.log(document.getElementById("preview1"));
                    var video = document.getElementById('preview2');
                    video.srcObject = null;
                    video.src = URL.createObjectURL(recordedBlob);
                    //video.style.display = 'flex';
                    self.cambiarVideos();
                    video.muted = false;
                    video.controls = true;
                    video.load();
                    video.onloadedmetadata = function(e) {
                        video.play();
                    };
                })            
            }).catch(function(err) {
                // deal with an error (such as no webcam)
                console.log(err);
            });
        },
    },
    mounted: function() {
        this.cargarCamara();
    }
}