<template>   
    <v-sheet
      id="dropzone"
      ref="dzone"
      tabindex="0"
      title="Click to grap a file from your PC!"
      color="indigo lighten-4"
      width="100%"
      style="cursor:pointer;"
      height="150"
      class="pa-2"
    >
      <input 
        ref="upload"
        id="fileUpload"
        type="file" 
        style="display:none"
        />
      <v-row justify="center">
        <v-icon
          v-if="!dragover" 
          color="indigo darken-2" 
          size="75"
        >mdi-cloud-upload-outline</v-icon>
        <v-icon
          v-if="dragover" 
          color="indigo darken-2" 
          size="75"
        >mdi-book-plus</v-icon>
      </v-row>
      <v-row justify="center">
        <span class="title indigo--text text--darken-2">Arrastra o da click para subir los archivos</span>
      </v-row>
    </v-sheet>
</template>
<script>
export default {
    data: () => ({
        formUpload: false,
        dragover: false
    }),
    methods: {
        filesSelected(fileList) {
          console.log('file selected event');
          this.$emit('files-selected',fileList);
          this.dragover = false
        },
    },
    mounted: function() {
      const dropzone = this.$el;
      const fileupload = this.$el.firstElementChild;
      // register listeners on your dropzone / v-sheet
      if(dropzone) {
        // register all drag & drop event listeners
        dropzone.addEventListener("dragenter", e => {
          e.preventDefault();
          this.dragover = true;
        })
        dropzone.addEventListener("dragleave", e => {
          e.preventDefault();
          this.dragover = false;
        })
        dropzone.addEventListener("dragover", e => {
          e.preventDefault();
          this.dragover = true;
        })
        dropzone.addEventListener("drop", e => {
          e.preventDefault();
          const dragevent = e;
          if(dragevent.dataTransfer) {
            this.filesSelected(dragevent.dataTransfer.files);
          }
        })
        // register event listener for keyboard usage
        dropzone.addEventListener("click", e => {
          if (e.target != fileupload) e.preventDefault();
          if(fileupload) {
            fileupload.click();
          }
        })
        dropzone.addEventListener("keypress", e => {
          e.preventDefault();
          const keyEvent = e;
          if (keyEvent.key === "Enter") {
            if(fileupload)
              fileupload.click();
          }
        })
        // register listeners on the file input
        if(fileupload) {
          fileupload.addEventListener("change", e => {
            const target = (e.target);
            if(target.files) {
              this.filesSelected(target.files);
            }
          })
      }
      }    
    }
}
</script>